import React from "react"
import * as footerStyle from '../../docs/footer/docsfooter.module.css'

const DocsFooter = () => 
{
    const getYear =  new Date().getFullYear() 

    const copyrightStr = `Copyright, ${getYear}`

    return (
        <footer className={ footerStyle.docsFooter }>
            { copyrightStr }
        </footer>
    )
}

export default DocsFooter