import React from "react"
import * as extLinkStyle from "./extlink.module.css"

const ExtLink = (props) => {
    const linkTo = props.to

    return (
        <>
            <a href={ linkTo } target="_blank" rel="noopener noreferrer">{ props.children } </a>
            <div className={ extLinkStyle.obliqueArrow }></div>
        </>
    )
}

export default ExtLink